.navbar {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  background: rgb(137, 255, 147);
  align-items: center;

  h1 {
    padding-left: 50px;
  }

  .navLinks {
    list-style-type: none;
    display: flex;
    align-items: center;

    .navLink {
      padding-right: 20px;
      text-decoration: none;
    }
  }
}
